.main {
    .main__info {
        color: $page_color;
        text-align: center;
        .main__title {
            margin-bottom: 12px;
            h1, span {
                font-weight: 500;
                font-size: 56px;
                line-height: 72px;
                text-align: center;
                
                span {
                  color: $color_active;
                }
            }
        }
        .main__text {
            font-size: 20px;
            line-height: 32px;
            margin-bottom: 12px;
        }
    }
    .main__video {
        width: 100%;
        height: 100%;
        object-fit: cover;
        -o-object-fit: cover;
        margin-top: 70px;

        img {
            width: 100%;
        }
    }
}